<template>
    <v-app>
        <!-- -->
        <v-img
        v-if="imageUrl" :src="imageUrl"
        max-height="200"
        max-width="100%"
        ></v-img>
        <!-- -->
        <v-img
        v-else
        max-height="200"
        max-width="100%"
        src="..\assets\banner.jpg"
        ></v-img>
    <br>
    <v-container style="width: 60%;">
    <v-layout column wrap>
        <v-flex>
            <v-row>
                <v-col cols="6" md="4">
                    <p style="font-size:20px" class="text-center"> </p>
                </v-col>
                <v-col cols="12" sm="6" md="8">
                    <v-text-field
                    label=" "
                    :rules="rules"
                    counter="100"
                    outlined
                    ></v-text-field>
                </v-col>
            </v-row>
        </v-flex>
        <v-flex>
            <v-row>
                <v-col cols="6" md="4">
                    <p style="font-size:20px" class="text-center"> tag</p>
                </v-col>
                <v-col cols="12" sm="6" md="8">
                    <v-combobox
                    v-model="model"
                    :items="items"
                    :search-input.sync="search"
                    hide-selected
                    outlined
                    label="Add some tags"
                    multiple
                    small-chips
                    >
                        <template v-slot:no-data>
                            <v-list-item>
                                <v-list-item-content>
                                    <v-list-item-title>
                                        No results matching "<strong>{{ search }}</strong>". Press <kbd>enter</kbd> to create a new one
                                    </v-list-item-title>
                                </v-list-item-content>
                            </v-list-item>
                        </template>
                    </v-combobox>
                </v-col>
            </v-row>
        </v-flex>
        <v-flex>
            <v-row>
                <v-col cols="6" md="4">
                    <p style="font-size:20px" class="text-center"> </p>
                </v-col>
                <v-col cols="12" sm="6" md="8">
                    <v-textarea
                    counter="1000"
                    outlined
                    label=" "
                    :rules="area_rules"
                    ></v-textarea>
                </v-col>
            </v-row>
        </v-flex>
        <v-flex>
            <v-row>
                <v-col cols="6" md="4">
                    <p style="font-size:20px" class="text-center">  </p>
                </v-col>
                <v-col cols="12" sm="6" md="8">
                    <input ref="imageInput" type="file" hidden @change="onChangeImages">
                    <v-btn type="button" @click="onClickImageUpload"> </v-btn>
                </v-col>
            </v-row>
        </v-flex>
        <v-flex>
            <v-row>
                <v-col cols="6" md="4">
                    <p style="font-size:20px" class="text-center">  </p>
                </v-col>
                <v-col cols="12" sm="6" md="8">
                    <v-radio-group
                    v-model="profile_radios"
                    :mandatory="false"
                    >
                        <v-radio
                        label="My "
                        value="radio-1"
                        ></v-radio>
                        <v-radio
                        label=" "
                        value="radio-2"
                        ></v-radio>
                    </v-radio-group>
                    <v-autocomplete
                    v-model="my.tag"
                    :items="items"
                    :disabled="profile_radios == 'radio-1'"
                    outlined
                    dense
                    chips
                    small-chips
                    label=""
                    multiple
                    ></v-autocomplete>
                    <v-textarea
                    label=""
                    v-model="my.career"
                    :disabled="profile_radios == 'radio-1'"
                    outlined
                    ></v-textarea>
                </v-col>
            </v-row>
        </v-flex>
        <v-flex>
            <v-row>
                <v-col cols="6" md="4">
                    <p style="font-size:20px" class="text-center">  </p>
                </v-col>
                <v-col cols="12" sm="6" md="8">
                    <v-radio-group
                    v-model="num_radios"
                    :mandatory="false"
                    >
                        <v-radio
                        label=""
                        value="radio-1"
                        ></v-radio>
                        <v-radio
                        label=""
                        value="radio-2"
                        ></v-radio>
                    </v-radio-group>
                    <v-text-field
                        v-model="membar_num"
                        v-show="num_radios == 'radio-2'"
                        type="number"
                        label=" "
                        min="2"
                        max="10"
                        outlined
                    ></v-text-field>
                </v-col>
            </v-row>
        </v-flex>
        <v-flex>
            <v-row>
                <v-col cols="6" md="4">
                    <p style="font-size:20px" class="text-center">   </p>
                </v-col>
                <v-col cols="12" sm="6" md="8">
                    <v-radio-group
                    v-model="funding_radios"
                    :mandatory="false"
                    >
                        <v-radio
                        label="  "
                        value="radio-1"
                        ></v-radio>
                        <v-radio
                        label=" "
                        value="radio-2"
                        ></v-radio>
                    </v-radio-group>
                    <v-text-field
                    label="  "
                    suffix=""
                    v-show="funding_radios == 'radio-2'"
                    value="0"
                    style="width:35%"
                    ></v-text-field>
                </v-col>
            </v-row>
        </v-flex>
        <v-flex>
            <v-row>
                <v-col cols="6" md="4">
                    <p style="font-size:20px" class="text-center">   </p>
                </v-col>
                <v-col cols="12" sm="6" md="8">
                    <v-radio-group
                    v-model="deadline_radios"
                    :mandatory="false"
                    >
                        <v-radio
                        label=""
                        value="radio-1"
                        ></v-radio>
                        <v-radio
                        label=" "
                        value="radio-2"
                        ></v-radio>
                    </v-radio-group>
                    <div v-show="deadline_radios == 'radio-2'">
                        <v-date-picker
                            v-model="date"
                            full-width
                            class="mt-4"
                        ></v-date-picker>
                    </div>
                </v-col>
            </v-row>
        </v-flex>
        <br>
        <v-flex>
            <div class="text-center">
            <v-btn
            v-model="fab"
            router :to="{name: 'CollaboProjMain'}"
            >
             </v-btn>
            </div>
        </v-flex>
    </v-layout>
    </v-container>
    </v-app>
</template>

<script>

export default {
    name: 'projectStart',
    components: {

    },

    data() {
        return{
            items: ['', '', '', 'IT', '','','','',','],
            rules: [v => v.length <= 100 || 'Max 100 characters'],
            area_rules: [v => v.length <= 1000 || 'Max 1000 characters'],
            member_num: '2',
            model:[],
            search: null,
            num_radios: 'radio-1',
            profile_radios: 'radio-1',
            deadline_radios: 'radio-1',
            funding_radios: 'radio-1',
            date: new Date().toISOString().substr(0, 10),
            my: {
                tag: ['', ''],
                career: '  '
            },
            imageUrl: null,
        }
    },
    methods: {
        onClickImageUpload() {
            this.$refs.imageInput.click();
        },
        onChangeImages(e) {
            console.log(e.target.files)
            const file = e.target.files[0];
            this.imageUrl = URL.createObjectURL(file);
        }
    },

}
</script>

        